body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Arial, sans-serif;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: linear-gradient(135deg, #2471a3,  #0e6655 );
  color: #fff;
  overflow: hidden;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px; /* Ajusta la altura de la cabecera */
  background-color: rgba(0, 0, 0, 0.5); /* Fondo semi-transparente para la cabecera */
}

.logo {
  height: 60px; /* Ajusta el tamaño del logo */
}

.marquee {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
  padding: 0 20px; /* Ajuste de padding para mejor visibilidad en los bordes */
  text-align: center; /* Centra el texto horizontalmente */
}

.text-wrap {
  display: flex; /* Cambiado a flex */
  flex-direction: column; /* Apila el título y el texto verticalmente */
  width: 100%;
  height: 100%; /* Asegura que el contenedor ocupe todo el espacio disponible */
  animation: scroll 45s linear infinite;
}

.title {
  font-size: 3em; /* Ajusta el tamaño del título */
  margin-bottom: 20px; /* Espacio entre el título y el texto */
  font-weight: bold; /* Estilo del título */
  color: #FFD700; /* Cambia el color del título (amarillo dorado) */
}

.text {
  font-size: 2.5em; /* Ajusta el tamaño del texto */
  line-height: 1.2; /* Ajusta la altura de línea para mejor legibilidad */
  word-wrap: break-word; /* Permite la ruptura de palabras largas si es necesario */
}

@keyframes scroll {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(-100%);
  }
}

@media (min-width: 768px) {
  .title {
    font-size: 4em; /* Tamaño más grande del título en pantallas mayores */
  }

  .text {
    font-size: 3em; /* Tamaño más grande en pantallas mayores */
  }

  .logo {
    height: 80px;
  }
}
